.searchbar {
  top: 0;
  left: 0;
  /* position: sticky; */
  z-index: 1100;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #fff;
}

.searchForm {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 600px;
  background-color: #fff;
  border-radius: 3px;
  overflow: hidden;
}

.searchFormButton {
  display: inline-block;
  width: 180px;
  height: 48px;
  border: 0;

  background-size: 40%;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.6;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  outline: none;
}

.searchFormButton:hover {
  opacity: 1;
}

.searchFormButtonLabel {
  font-size: 18px;
}

.searchFormInput {
  display: inline-block;
  width: 100%;
  font: inherit;
  font-size: 16px;
  border: none;
  outline: none;
  padding-left: 10px;
  padding-right: 4px;
}

.searchFormInput::placeholder {
  font: inherit;
  font-size: 18px;
}
